@import '../../styles/mixin.sass';

.card {
  @include card;
  // border-color: blue
  position: absolute; }
.drop_card {
  @include card($isDropCard: true);
  // border-color: blue
  position: absolute; }
.right_top_image {
  width: 100%;
  height: 100%;
  position: absolute; }
.left_bottom_image {
  width: 100%;
  height: 100%;
  position: absolute; }
.card_number {
  margin-left: 10%;
  margin-top: 6%;
  font-weight: bold;
  font-size: 1.05rem; }
