@import '../../styles/var.sass';

.card_column_container {
  @media screen and (min-width: 1600px) {
    width: map-get($card-width, max); }
  @media screen and (max-width: 1600px) {
    width: map-get($card-width, min); }
  position: relative;
  box-sizing: border-box;
  &:last-child {
    margin-right: 0px; } }
